.status-circle-base {
  border-radius: 50%;
  width: 24px;
  height: 24px;
}

.status-circle-open {
  color: rgb(65, 182, 127);
  font-size: 1rem;
}
.status-circle-pending {
  color: rgb(248, 179, 68);
  font-size: 1rem;
}
.status-circle-resolved {
  color: rgb(127, 58, 171);
  font-size: 1rem;
}
.status-circle-closed {
  color: rgb(41, 177, 195);
  font-size: 1rem;
}
.urgency-indicator {
  width: 25px;
  height: 25px;
  border-radius: 5px;
}
.urgency-1 {
  color: white;
  background: rgb(250, 86, 86);
}
.urgency-2 {
  color: white;
  background-color: rgb(248, 179, 68);
}
.urgency-3 {
  color: white;
  background-color: rgb(65, 182, 127);
}
