.TicketListPage {
  min-height: 100vh;
  padding-bottom: 24px;
  padding-left: 20px;

  .header-label {
    margin-top: 24px;
  }

  .title {
    font-size: 1.75rem;
    align-items: flex-start;
    font-weight: 700;
    color: #272E4C;
  }

  .link-button {
    color: #3AB77D;
    font-size: 1rem;
    font-weight: bold;

    .link-button-icon {
      color: #3AB77D;
      font-size: 0.9rem;
      margin-right: 12px;
    }

    .link-button-text {
      text-decoration: underline;
    }
  }

  .search {
    margin-top: 24px;

    .search-field {
      background-color: white;
    }

    .input-adornment {
      color: #00000061;
      margin-right: 8px;
    }
  }

  .column-selection {
    font-size: 1rem;

    .column-selection-icon {
      font-size: 1rem;
      margin-right: 12px;
    }
  }

  .tickets-table {
    margin-top: 16px;
    max-width: 89vw;

    .tickets-table-container {
      background-color: white;
    }

    .tickets-table-stats {
      margin-top: 16px;
      margin-bottom: 16px;

      .tickets-all {
        font-size: 1.2rem;
        margin-right: 8px;
        font-weight: bold;
        color: rgb(39, 46, 76);
      }

      .tickets-open {
        font-size: 1.2rem;
        margin-right: 8px;
        font-weight: bold;
        color: rgb(65, 182, 127);
      }
      .tickets-pending {
        font-size: 1.2rem;
        margin-right: 8px;
        font-weight: bold;
        color: rgb(248, 179, 68);
      }
      .tickets-resolved {
        font-size: 1.2rem;
        margin-right: 8px;
        font-weight: bold;
        color: rgb(127, 58, 171);
      }
      .tickets-closed {
        font-size: 1.2rem;
        margin-right: 8px;
        font-weight: bold;
        color: rgb(41, 177, 195);
      }
      .tickets-default {
        font-size: 1.2rem;
        margin-right: 8px;
        font-weight: bold;
        color: rgba(39,46,76,0.5);
      }
      .ticket-label {
        text-decoration: underline;
        cursor: pointer;
        color: rgba(39,46,76,0.5);
      }
      .ticket-label-active {
        text-decoration: underline;
        cursor: auto;
        color: inherit;
      }
    }

    tr:nth-of-type(odd) {
      background-color: white;
    }

    tr:nth-of-type(even) {
      background-color: rgb(246, 247, 250);
    }

    th {
      background-color: white;
      div {
        div {
          align-content: center;
        }
      }
    }

    td {
      background-color: inherit;
    }

    [class*="TableFixedCell-dividerRight"] {
      border-right: 2px solid #9EA3AC;
    }

    .MuiOutlinedInput-notchedOutline {
      background-color: #FFFFFF;
    }
  }
}
